.fieldquote__container{max-width:400px;margin:0 auto}.fieldquote__box{background-color:#fff;display:flex;flex-direction:column;padding:40px}.fieldquote__title{transition:color .5s;font-size:28px;font-weight:500;opacity:0}.fieldquote__title::before{content:"‚ùù";margin-right:10px}.fieldquote__author{transition:color .5s;align-self:flex-end;margin:20px 0;opacity:0}.fieldquote__author::before{content:"- "}.fieldquote__button{align-self:flex-end;padding:10px;border-radius:5px;color:#fff;cursor:pointer;font-size:18px;transition:background-color .5s}.active{transition:opacity 1.5s;opacity:1}